


























































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import {ClientV1 as Multiplayer} from '@javfres/escaperoom-multiplayer';
import {Debounce} from 'lodash-decorators';

const SERVER = 'https://multiplayer.javilabs.es';

@Component
export default class IntroMultiplayerVue extends Vue {

    @Prop()
    private join_game_id!: string|null;

    multiplayer: Multiplayer|null = null;
    join_url: string = '';

    user_list: string[] = [];
    my_name = '';


    mounted(){
        
        if(this.join_game_id){
            this.join_game(this.join_game_id);
        } else {
            this.create_game();
        }

    }


    async create_game(){
        
        const multiplayer = new Multiplayer(SERVER);
        await multiplayer.create();
        this.multiplayer = multiplayer;

        this.after_joining();

    }

    async join_game(game_id: string){
        const multiplayer = new Multiplayer(SERVER);
        await multiplayer.join(game_id);
        this.multiplayer = multiplayer;

        this.after_joining();

    }

    after_joining(){

        this.multiplayer!.onUserData(this.update_game);

        this.join_url = window.location.href.split('?')[0];
        this.join_url += '?join=' + this.multiplayer!.game_id;

        this.multiplayer!.on('chat', this.chat_recv.bind(this));

        this.update_game();

    }



    update_game(){

        if(!this.multiplayer) return;
        this.user_list = this.multiplayer!.user_list.map((x,i) => {

            const data = this.multiplayer!.user_data[i];
            const name = data.name ? data.name : 'Sin nombre';

            return name;

        });

    }


    @Watch('my_name')
    @Debounce(200)
    change_name(name:string) {

        if(!this.multiplayer) return;
        this.multiplayer.emitUserData({name});
        this.update_game();

    }



    chat = '';
    chat_line = '';

    chat_send(){
        if(!this.chat_line) return;
        const msg = this.my_name + ": " + this.chat_line;
        this.chat_line = '';
        this.multiplayer!.emit('chat', msg);
        this.chat += msg + '\n';
    }

    chat_recv(msg:string){
        this.chat += msg + '\n';
    }


}

