




































































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

// Vue elements
import IntroMultiplayerVue from './IntroMultiplayer.vue';


@Component({
    components: {
        IntroMultiplayerVue
    },
})
export default class IntroVue extends Vue {


    private mode = 'select-game';
    private join_game_id: string|null = null;


    mounted(){
        
        const params = new URLSearchParams(window.location.search);
        const game_id = params.get('join');
        
        if(game_id){
            this.join_game_id = game_id;
            this.mode = 'multiplayer';
        }

    }

    start_singleplayer(){
        this.$emit('start-singleplayer');
    }

    create_multiplayer(){
        this.mode = 'multiplayer';
    }

}

